@import 'https://use.fontawesome.com/releases/v5.5.0/css/all.css';
@import 'mixins.scss';

@media screen and (max-width: 1024px){
    html{
        font-size: 14px;
    }
    .card{
        .info{
            width: 85%;
        }
    }
}

@media screen and (max-width: 960px){
    html{
        font-size: 13px;
    }
    .contacts{
        font-size: 1.4rem;
        .about{
            width: 42.5%;
        }
        .partnership{
            width: 42.5%;
        }
    }
    .menu{
        .card{
            width: 27vw;
            height: 42vw;
            max-width: none;
            max-height: none;
            margin-bottom: 1.5rem; 
        }
        .card:nth-child(odd){
            margin-right: 4rem;
        }
        .card:nth-child(even){
            margin-right: 0;
        }
        .type{
            padding: 0 3.5rem;
        }
    }
    
}

@media screen and (max-width: 768px){
    .content{
        .introduction{
            padding: 2rem;
            .description{
                width: 90%;
            }
        }
    }
    .menu{
        .card{
            .info{
                width: 92.5%;
                height: 95%;
            }
        }
    }
    .contacts{
        flex-direction: column;
        .separator{
            width: 95%;
            height: 3px;
            margin: 2rem 0;
        }
        .about{
            width: 75%;
        }
        .partnership{
            width: 75%;
        }
    }
}

@media screen and (max-width: 640px){
    html{
        font-size: 12px;
    }
    .menu{
        .card{
            width: 35vw;
            height: 57vw;
        }
        .card:nth-child(odd){
            margin-right: 2rem;
        }
        .card:nth-child(even){
            margin-right: 0;
        }
        .type{
            padding: 0 2rem;
        }
    }
    .content{
        .description{
            font-size: 1.4rem !important;
            .logo{
                font-size: 1.2rem;
            }
        }
    }
    .nav{
        
        position: relative;
        .nav-links{
            display: none;
            li a{
                font-size: 2rem;
            }
            li{
                margin-bottom: 0.5rem;
            }
            li:last-of-type{
                margin-bottom: 0;
            }
        }
        i{
            z-index: 1010;
        }
          
        label{
            i{
                display: inline-block;
              position: absolute;
              top: 50%;
              right: 5%;
              font-size: 2.5rem;
              transform: translateY(-50%);
            }
          }
    }
    #menu-toggle {
        display: none;
      }
      
      #menu-toggle:checked + .nav{
        height: auto;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 1000;
      }
      
      #menu-toggle:checked + .nav i:before{
        content: '\f00d';
      }

      #menu-toggle:checked + .nav i{
          top: 5% !important;
      }
      
      #menu-toggle:checked + .nav .nav-links{
        @include delay;
        
        background: #d8d6de;
        z-index: 1001;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }

      #menu-toggle:checked + .nav .nav-links li{
        opacity: 0;
        animation: menu-in 0.4s linear forwards;
      }
}



@media screen and (max-width: 480px){
    .content{
        .introduction{
            .heading{
                h2{
                    font-size: 2rem;
    
                }
            }
            .description{
                font-size: 1.3rem;
                .logo:last-of-type{
                    font-size: 1.1rem;
                }
            }
            
        }
    }
    .menu{
        .card{
            width: 60vw;
            height: 93vw;
            .info{
                font-size: 1.4rem; 
            }
        }
        .card:nth-child(odd){
            margin-right: 0;
        }
        .card:nth-child(even){
            margin-right: 0;
        }
        .type{
            padding: 0 2rem;
        }
    }
    .contacts{
        .about,.partnership{
            width: 90%;
        }
    }
}